import process from "process"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { stdoutWrite } from "../../core/StdoutWrite"
import { LineReader } from "../../core/fs/LineReader"
import { blank } from "../../fe/Blank"
import { errorErrno, errorToJson } from "../../fe/Error"
import { stringify } from "../../fe/JSON"
import { parseJSON } from "../../fe/ParseMaybe"
import { toS } from "../../fe/toS"
import { exit } from "../async/Exit"
import { ChildServiceExitCommand } from "../child/ChildServiceExitCommand"
import { onError } from "../error/OnError"
import { WrappedError } from "../error/WrappedError"
import {
  WorkRequest,
  WorkRequestMethod,
  WorkerFunctions,
  isWorkRequest
} from "./WorkRequests"

const logger = lazy(() => mkLogger("worker.Worker"))

export async function handleWorkRequest(msg: WorkRequest<WorkRequestMethod>) {
  const shim = WorkerFunctions[msg?.method]
  if (shim == null) {
    throw new Error("invalid method " + stringify(msg?.method))
  } else {
    return shim(msg.args[0] as any)
  }
}

function setupSignalHandlers() {
  for (const signal of ["SIGINT", "SIGTERM"]) {
    process.on(signal, reason => exit({ reason, status: 0 }))
  }
}

function setupErrorHandlers() {
  for (const ea of ["uncaughtException", "uncaughtException", "disconnect"]) {
    process.on(ea, cause => {
      const error = new WrappedError("uncaughtException", { cause })
      return onError(error)
        ? exit({
            error,
            status: errorErrno(error) ?? 1
          })
        : undefined
    })
  }
}

function setupChildProcessWorker() {
  const rl = process.stdin.pipe(new LineReader())
  rl.on("data", onStdinData)

  logger().info("Child process mode: listening on stdin...")
}

export function handleWorkRequests() {
  if (true === process.stdin?.readable) {
    setupSignalHandlers()
    setupErrorHandlers()
    setupChildProcessWorker()
  } else {
    void exit({
      reason: "process.stdin is not readable",
      status: 13
    })
  }
}

async function onStdinData(line: any) {
  line = toS(line)
  if (blank(line)) return
  if (line.trim() === ChildServiceExitCommand) {
    return exit({ reason: line + " from stdin", status: 0 })
  }
  const request = parseJSON(line) as any
  logger().debug("onData", { request })
  if (!isWorkRequest(request)) {
    // MUST BE KEPT IN SYNC WITH ./RequestTask!
    logger().warn("not a work request", { request, line })
    stdoutWrite(
      { id: request?.id, request, error: "invalid work request" },
      true
    )
  } else {
    try {
      const response = await handleWorkRequest(request)
      stdoutWrite({ id: request.id, response }, true)
    } catch (err: any) {
      const error = errorToJson(err)
      logger().warn("failed to process request", { request, error })
      stdoutWrite(
        {
          id: request.id,
          error
        },
        false
      )
    }
  }
}
