import process from "process"
import { stringify } from "../fe/JSON"
import { ServiceNames } from "../fe/ServiceNames"
import { addDefaultJsonRevivers } from "./JsonSetup"
import { setProcessTitle, setShortProcessNames } from "./ProcessTitle"
import { setServiceName } from "./SetServiceName"
import { stdoutWrite } from "./StdoutWrite"
import { exitOnStdStream } from "./async/Exit"
import { cliWrap } from "./cli/CliWrap"
import { DescriptionFooter } from "./cli/DescriptionFooter"
import { joinLines } from "./fs/CRLF"
import { onProgressEvt } from "./progress/ProgressObserver"
import { Settings } from "./settings/Settings"
import { volumes } from "./volumes/Volumes"
import { handleWorkRequests } from "./worker/Worker"

// We're skipping Service to try to be as lightweight as possible:

async function run() {
  if (process.argv.includes("--help")) {
    console.log(
      joinLines(
        ...cliWrap(
          `This is PhotoStructure's background worker, and is for internal use only.\n${DescriptionFooter}`
        )
      )
    )
    process.exit(0)
  }

  // We're not using Service or CLI here to minimize dependencies and keep
  // worker lightweight

  // we just set up the service name and the logger...
  setServiceName(ServiceNames.worker)
  setShortProcessNames(Settings.shortProcessNames.valueOrDefault)
  setProcessTitle() // normally done by Service._setup()

  // for testing only: verify that cached volumes works:
  if (process.argv.includes("--volumes")) {
    console.log(stringify(await volumes()))
    process.exit(0)
  }

  addDefaultJsonRevivers() // normally done by Service._setup()
  exitOnStdStream()

  // don't set up sharedState.instance(): workers should be oblivious to
  // events and tasks.

  onProgressEvt.setShim(async pe => {
    // tell parent we're doing work...
    stdoutWrite(pe, undefined) // < don't say we're ready!
  })

  // don't load settings! All non-defaults will be in our env.
  handleWorkRequests()
}

void run()
